import React, { useState, useEffect } from 'react'
import cx from 'classnames'

import NumberPicker from 'components/NumberPicker'
import Button from 'components/Button'
import { connectMetamask, mintAndro, mintBoys, mintGirls, presaleAndro, presaleBoys, presaleGirls } from 'helpers/web3'

import './styles.scss'
// import { Context } from 'react-responsive'

interface ShopCardTypes {
  images: Array<string>;
  title: string;
  price: string;
  description: Array<string>;
  type?: any;
}

const ShopCard = ({ type, images, title, price, description }: ShopCardTypes) => {
  const [imageIndex, setImageIndex] = useState(0)
  const [account, setAccount] = useState("");
  const [counter, setCounter] = useState(1)

  useEffect(() => {
    const intervalName = setInterval(() => {
      const imagesNumber = images.length
      const getNextIndex = (value: number) => imagesNumber === (value + 1) ? 0 : value + 1
      setImageIndex(value => getNextIndex(value))
    }, 250)

    return () => clearInterval(intervalName)
  }, [])

  const handleConnect = async () => {
    const account = await connectMetamask();
    setAccount(account);
  };

  const handleBuy = () => {
    switch (type) {
      case 0:
        presaleBoys(setError)
        // mintBoys(counter)
        break;
      case 1:
        presaleGirls(setError)
        // mintGirls(counter)
        break;
      case 2:
        presaleAndro(setError)
        // mintAndro(counter)
        break;
    }
  }

  const [error, setError] = useState("");

  return (
    <div className="shop-card">
      <div className="shop-card__image-wrapper">
        <div className="shop-card__image-unknown">?</div>
        {
          images.map((img, index) => (
            <img
              key={index}
              className={cx('shop-card__image', { 'shop-card__image--active': index === imageIndex })}
              src={images[index]} alt=""
            />
          ))
        }
      </div>
      <div className="shop-card__content">
        <div className="shop-card__content-head">
          <h3 className="shop-card__content-title">{title}</h3>
          <h4 className="shop-card__content-price">{price}</h4>
        </div>
        {
          description.map((item, index) => (
            <p
              key={index}
              className="shop-card__content-description"
              dangerouslySetInnerHTML={{ __html: item }}
            ></p>
          ))
        }
      </div>
      <NumberPicker counter={counter} setCounter={setCounter} />
      {
        account ? 
          <Button onClick={handleBuy} className="shop-card__submit">{error || "BUY"}</Button>
        :
          <Button onClick={handleConnect} className="shop-card__submit">Connect wallet</Button>
      }
    </div>
  )
}

export default ShopCard