import Web3 from "web3";
import JSBI from "jsbi";
import { boysAbi, girlsAbi, boysContract, girlsContract, androAbi, androContract } from "./contracts"
import { getHexProof } from "./merkle";

export async function getAccounts() {
  if (!(window as any)?.ethereum) return;
  try {
    let acc = await (window as any)?.ethereum?.request({
      method: "eth_requestAccounts",
    });

    return acc;
  } catch (e) {
    return [];
  }
}

const checkNetwork = async () => {
  try {
    await (window as any)?.ethereum.request({
      method: 'wallet_switchEthereumChain',
      params: [{ chainId: '0x1' }]
    })
    return true
  } catch (error) {
    console.error(error)
    return false
  }
}

export async function connectMetamask() {
  if ((window as any)?.ethereum) {
    try {
      const result = await getAccounts();
      if (Array.isArray(result) && result.length > 0) {
        let acc = result[0];
        checkNetwork()
        return acc;
      } else {
        return false;
      }
    } catch (err) {
      return false;
    }
  } else {
    return false;
  }
}

const cost = "50000000000000000";
const androCost = "80000000000000000";

var web3 = new Web3(
  new Web3.providers.HttpProvider(
      "https://mainnet.infura.io/v3/ab8d96893fbd4f5b88c6c89d5da48584"
      // "https://data-seed-prebsc-1-s1.binance.org:8545/"
  )
);

export async function mintBoys(totalToMint: any) {
  if (!(window as any)?.ethereum) return;

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const contract = new web3.eth.Contract(boysAbi as any, boysContract);
  const address = (await getAccounts())[0];
  // const balance = await web3.eth.getBalance(address);

  // if (
  //   JSBI.greaterThan(JSBI.BigInt(balance), JSBI.multiply(
  //     JSBI.BigInt(cost),
  //     JSBI.BigInt(totalToMint.toString())
  //   ))
  // ) {
  //   console.log("low balance");
  //   return false
  // }
  // console.log(balance);
  const transactionParameters = {
    to: boysContract,
    from: address,
    value: JSBI.multiply(
      JSBI.BigInt(cost),
      JSBI.BigInt(totalToMint.toString())
    ).toString(16),
    data: contract.methods.mintBoys(totalToMint).encodeABI(),
  };

  try {
    await (window as any)?.ethereum?.request({
      method: "eth_sendTransaction",
      params: [transactionParameters],
    });
    return true
  } catch (error) {
    console.log(error);
  }
}

