export async function presaleBoys(setError: any) {
  if (!(window as any)?.ethereum) return;

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const contract = new web3.eth.Contract(boysAbi as any, boysContract);
  const address = (await getAccounts())[0];

  const hexProof = getHexProof(address)
  if (hexProof === -1) {
    setError("Not whitelisted");
    return;
  }

  const transactionParameters = {
    to: boysContract,
    from: address,
    value: JSBI.BigInt(cost).toString(16),
    data: contract.methods.presaleBoys(hexProof).encodeABI(),
  };

  try {
    await (window as any)?.ethereum?.request({
      method: "eth_sendTransaction",
      params: [transactionParameters],
    });
    return true
  } catch (error) {
    console.log(error);
  }
}

