import React, { useState } from 'react'

import './styles.scss'

interface NumberPickerProps {
  counter: number;
  setCounter: Function;
}

const NumberPicker = ({ counter, setCounter }: NumberPickerProps) => {

  const handlePlus = () => {
    setCounter(counter + 1 > 10 ? 10 : counter + 1)
  }
  const handleMinus = () => setCounter(Number(counter === 1 ? counter : counter - 1))
  const handleChange = (event: any) => {
    const valueNumber = Number(event.target.value)
    const validateValue = valueNumber < 1 ? 1 : valueNumber > 10 ? 10 : valueNumber
    setCounter(validateValue)
  }
  const handleSet5 = (addValue: number) => setCounter(5)
  const handleSet10 = (addValue: number) => setCounter(10)

  return (
    <div className="number-picker" onMouseDown={event => event.stopPropagation()}>
      <div className="number-picker__input-wrapper">
        <div className="number-picker__input-button" onClick={handleMinus}>
          -
        </div>
        <input type="number" className="number-picker__input" value={counter} onChange={handleChange} max="10" />
        <div className="number-picker__input-button" onClick={handlePlus}>
          +
        </div>
      </div>
      <div
        className="number-picker__input-button number-picker__button"
        onClick={() => handleSet5(5)}
      >
        5
      </div>
      <div
        className="number-picker__input-button number-picker__button"
        onClick={() => handleSet10(10)}
      >
        10
      </div>
    </div>
  )
}

export default NumberPicker