import React from 'react'

import { useMediaQuery } from 'react-responsive'

import shopDecriptionBg from 'assets/images/shop-description-bg.png'
import shopDecriptionBgMobile from 'assets/images/shop-description-bg-mobile.png'

import './styles.scss'

const translates = {
  desktop: {
    title1: 'Everybody wants to have sex. Our SEX NFTs want that too!',
    title2: 'Join our new meta-universe full of love, passion and excitement.',
    description1: 'Presale 10 January 2022',
    description2: 'Public 14 January 2022 11:00 UTC',
  },
  mobile: {
    title1: 'Everybody wants to have sex.<br/> Our SEX NFTs want that too!',
    title2: 'Join our new meta-universe<br/> full of love, passion<br/> and excitement.',
    description1: 'Presale 10 January 2022',
    description2: 'Public 14 January 2022 11:00 UTC',
  }
}

const ShopDescription = () => {
  const isMobile = useMediaQuery({
    query: '(max-width: 460px)'
  })
  
  const t = isMobile ? translates.mobile : translates.desktop

  return (
    <div className="shop-description">
      <div className="container">
        <span className="shop-description__title" dangerouslySetInnerHTML={{__html: t.title1 }} />
        <span className="shop-description__title" dangerouslySetInnerHTML={{__html: t.title2 }} />
        <span className="shop-description__label" dangerouslySetInnerHTML={{__html: t.description1 }} />
        <span className="shop-description__label" dangerouslySetInnerHTML={{__html: t.description2 }} />
        <img className="shop-description__bg" src={isMobile ? shopDecriptionBgMobile : shopDecriptionBg} alt="Shop descriotion bg" />
      </div>
    </div>
  )
}

export default ShopDescription
