const { MerkleTree } = require('merkletreejs')
const keccak256 = require('keccak256')

const whitelist = require('./whitelist').whitelist

const leafNodes = whitelist.map(addr => keccak256(addr));

const merkleTree = new MerkleTree(leafNodes, keccak256, { sortPairs: true });

// const rootHash = merkleTree.getRoot();

// const address = leafNodes[7];

export const getHexProof = (address) => {
  let index = -1;

  for(let i=0; i<whitelist.length; i++) {
    if (whitelist[i].toLowerCase() === address.toLowerCase()) {
      index = i;
    }
  }
  if (index === -1) return -1;
  
  const leaf = leafNodes[index]
  const hexProof = merkleTree.getHexProof(leaf)

  return hexProof 
} 

