import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react'
import { useMediaQuery } from 'react-responsive'

import ShopCard from 'modules/Shop/components/ShopCard'

import { SHOP_CARDS } from 'consts/shop'

import './styles.scss'

const Shop = () => {
  const isMobile = useMediaQuery({
    query: '(max-width: 460px)'
  })

  return (
    <div className="shop">
      <div className="container">
        <div className="shop__pc">
          {!isMobile && (
            SHOP_CARDS.map((item, index) => (
              <ShopCard type={index} key={index} {...item} />
            ))
          )}
        </div>
        {isMobile && <Swiper
            navigation
            spaceBetween={0}
            slidesPerView={1}
            className="shop__slider"
          >
            {SHOP_CARDS.map((item, index) => (
              <SwiperSlide key={index}>
                <ShopCard type={index} {...item} />
              </SwiperSlide>
            ))}
          </Swiper>}
      </div>
    </div>
  )
}

export default Shop